import {ajaxErrorLog,dialogHandle} from '../dialog';
function masterBusinessDataDayHandle(options){
    return {
        type:"MASTERBUSINESSDARADAY_HANDLE",
        options
    }
}

function masterBusinessDataDayLoadData(options){
	return dispatch=>{
		$.XlAjax({
            url: "nodetradedetailDailyReport",
            type:"GET",
            data: options.data,
            success: function(response){
            	if(response.status == "0000"){
            		let dataList = response.data.data;
            		if(dataList.length>0){            			
            			dispatch(
            				masterBusinessDataDayHandle({
		                        tableList:dataList,
		                        tbodyList:"success",
		                        totalSize:response.data.count
		                    })
            			);
            		}else{
            			dispatch(
            				masterBusinessDataDayHandle({
		                        tableList:dataList,
		                        tbodyList:"nothing",
		                        totalSize:response.data.count
		                    })
            			);
            		}            		
            	}else{
            		dispatch(
            			masterBusinessDataDayHandle({
	                        tbodyList:"fail"
	                    })
            		);
            		dispatch(
            			dialogHandle({
            				show:true,
	                        type:"tips",
	                        tipsType:'fail',
	                        width:'300px',
	                        height:'300px',
	                        content:response.message,
	                        title:'提示',
            			})
            		);
            	}
            	
            },
            errorDialog:function(xhr, errorType, error){
                ajaxErrorLog(xhr, errorType, error,dispatch);
            }
        });
	}
}

function masterBusinessDataDayExport(options){
    return dispatch=>{
        $.XlAjax({
            url:"nodetradedetailDailyExportExcel",
            type:"GET",
            data:options,
            success:function(data){
                if(data.status=="0000"){
                    window.location.href=data.data.url;
                }else{
                    dispatch(dialogHandle({
                        show:true,
                        type:'tips',
                        tipsType:'warning',
                        content:data.message
                    }))
                }
            },
            errorDialog:function(xhr, errorType, error){
                ajaxErrorLog(xhr, errorType, error,dispatch);
            }
        })
    }
}



export  {masterBusinessDataDayHandle,masterBusinessDataDayLoadData,masterBusinessDataDayExport};